export function toast(title){
	uni.showToast({
		icon:'none',
		title:title
	})
}
/**
 * 
 */
export function checkPhone(value){
	let reg =/^1\d{10}$/
	if(reg.test(value)){
		return true;
	}
	toast('');
	return false
}
/**
 * 
 */
export function checkPwd(value){
	if(value.length>=6){
		return true;
	}
	toast('6');
	return false
}
/**
 * 
 */
export function checkCode(value){
	if(value.length==5){
		return true;
	}
	toast('5');
	return false
}
/**
 * 
 */
export function checkIdCard(value){
	let reg =/^\d{15}|\d{18}$/
	if(reg.test(value)){
		return true;
	}
	toast('1518');
	return false
}
/**
 * 
 */
export function checkBankCard(value){
	let reg =/^([1-9]{1})(\d{14}|\d{15}|\d{16}|\d{18})$/
	if(reg.test(value)){
		return true;
	}
	toast('');
	return false
}